import request from '@/plugin/axios'
import publicService from "@/api/sys/service.js";

const service = {
    ...publicService,
    /**
     * 获取网格编码
     * 
     * @param {any} successFunc 回调函数
     */
    requestGridCode(successFunc) {
        request({
            url: "/grid/rstu/find/all/code",
            method: "get",
        }).then((res) => {
            res.region = _.sortBy(res.region, "code");
            let code = constants.REGION.CODE;
            let region = {
                code: code,
                label: constants.REGION.NAME,
                disabled: code.length <= 9 ? true : false,
                children: []
            };
            let itemONE = {};
            switch (code && code.length) {
                case 4:
                    // 组织县级区域
                    itemONE = _.map(res.region, county => {
                        return {
                            code: county.code,
                            label: county.name,
                            disabled: true,
                            children: []
                        };
                    })
                    // 组织街道办区域
                    _.map(itemONE, item => {
                        item.children = _.map(_.filter(res.street, street => {
                            return street.code.indexOf(item.code) >= 0
                        }), street => {
                            return {
                                code: street.code,
                                label: street.name,
                                disabled: true,
                                children: []
                            };
                        })
                    })
                    // 组织社区区域
                    _.map(itemONE, county => {
                        _.map(county.children, item => {
                            item.children = _.map(_.filter(res.community, community => {
                                return community.code.indexOf(item.code) >= 0
                            }), community => {
                                return {
                                    code: community.code,
                                    label: community.name,
                                    children: []
                                };
                            })
                        })
                    })
                    // 组织单元网格区域
                    _.map(itemONE, county => {
                        _.map(county.children, street => {
                            _.map(street.children, item => {
                                item.children = _.map(_.filter(res.unit, unit => {
                                    return unit.code.indexOf(item.code) >= 0
                                }), unit => {
                                    return {
                                        code: unit.code,
                                        label: unit.name
                                    };
                                })
                            })
                        })
                    })
                    break;
                case 6:
                    // 组织街道办
                    itemONE = _.map(_.filter(res.street, street => {
                        return street.code.indexOf(code) >= 0
                    }), street => {
                        return {
                            code: street.code,
                            label: street.name,
                            disabled: true,
                            children: []
                        };
                    });
                    // 组织社区
                    _.map(itemONE, item => {
                        item.children = _.map(_.filter(res.community, community => {
                            return community.code.indexOf(item.code) >= 0
                        }), community => {
                            return {
                                code: community.code,
                                label: community.name,
                                children: []
                            };
                        })
                    })

                    // 组织网格
                    _.map(itemONE, county => {
                        _.map(county.children, item => {
                            item.children = _.map(_.filter(res.unit, unit => {
                                return unit.code.indexOf(item.code) >= 0
                            }), unit => {
                                return {
                                    code: unit.code,
                                    label: unit.name,
                                };
                            })
                        })
                    })
                    break;
                case 9:
                    // 组织社区
                    itemONE = _.map(_.filter(res.community, community => {
                        return community.code.indexOf(code) >= 0
                    }), community => {
                        return {
                            code: community.code,
                            label: community.name,
                            children: []
                        };
                    })
                    // 组织网格
                    _.map(itemONE, item => {
                        item.children = _.map(_.filter(res.unit, unit => {
                            return unit.code.indexOf(item.code) >= 0
                        }), unit => {
                            return {
                                code: unit.code,
                                label: unit.name
                            };
                        })
                    })
                    break;
                case 12:
                    // 组织网格
                    itemONE = _.map(_.filter(res.unit, unit => {
                        return unit.code.indexOf(code) >= 0
                    }), unit => {
                        return {
                            code: unit.code,
                            label: unit.name,
                        };
                    })
                    break;
                default:
                    break;
            };
            region.children = itemONE;
            successFunc(region);
        })
    },
    /**
     * 分页获取楼栋信息
     * 
     * @param {any} param 分页参数
     * @param {any} successFunc 回调函数
     */
    requestPageBuild(param, successFunc) {
        request({
            url: "/compre/hp/buildinfo/findPage?districtCode="+ param.districtCode +"&name="+ param.name +"&page="+ param.page +"&pageSize="+ param.pageSize,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 添加楼栋信息
     * 
     * @param {any} param 楼栋参数
     * @param {any} successFunc 回调函数
     */
    requestSaveBuild(param, successFunc) {
        request({
            url: "/compre/hp/buildinfo/save",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 修改楼栋信息
     * 
     * @param {any} param 楼栋信息参数
     * @param {any} successFunc 回调函数
     */
    requestUpdateBuild(param, successFunc) {
        request({
            url: "/compre/hp/buildinfo/update",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    
    /**
     * 删除楼栋信息
     * 
     * @param {any} buildId 楼栋 ID
     * @param {any} successFunc 回调函数
     */
    requestDeleteBuild(buildId, successFunc) {
        request({
            url: "/compre/hp/buildinfo/delete?id=" + buildId,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 添加户信息
     * 
     * @param {any} param 户信息参数
     * @param {any} successFunc 回调函数
     */
    requestSaveHouse(param, successFunc) {
        request({
            url: "/compre/hp/house/save",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 修改户信息
     * 
     * @param {any} param 户信息参数
     * @param {any} successFunc 回调函数
     */
    requestUpdateHouse(param, successFunc) {
        request({
            url: "/compre/hp/house/update",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 删除户信息
     * 
     * @param {any} param 户信息ID
     * @param {any} successFunc 回调函数
     */
    requestDeleteHouse(houseId, successFunc) {
        request({
            url: "/compre/hp/house/delete?id=" + houseId,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 查询户下户籍人列表
     * 
     * @param {any} houseId 
     * @param {any} successFunc 
     */
    requsetHousePeopleList(houseId, successFunc) {
        request({
            url: "/compre/hp/people/house/findPage?houseId="+ houseId +"&page=1&pageSize=100",
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },

    /**
     * 查询当前户下包含的经济企业、社会组织、出租房、学校等信息
     * 
     * @param {any} param 查询参数
     * @param {any} successFunc 回调函数
     */
    requestOrgList(param, successFunc) {
        request({
            url: "/compre/"+ param.url +"/findPage?page=1&pageSize=1000&houseId="+ param.houseId +"&districtCode=" + param.districtCode,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 批量创建户信息
     * 
     * @param {any} param 创建户信息参数
     * @param {any} successFunc 回调函数
     */
    requestMadeHouse(param, successFunc) {
        request({
            url: "/compre/hp/house/save/beach",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
};

export default service;