<template>
    <div class="charts-page">
        <div class="top-card">
            <techo-card resizeEvent="topCard" @onUpLevel="handleUpLevel" :maxCard="true" :upLevel="isShowLevelBtu">
                <div slot="header">房屋信息</div>
                <div id="TopBar" style="height:100%"></div>
            </techo-card>
        </div>
        <el-row class="bottom-card" :gutter="10">
            <el-col :span="12">
                <techo-card resizeEvent="leftCard" :maxCard="true">
                    <div slot="header">房屋类型</div>
                    <div id="LeftPie" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="12">
                <techo-card resizeEvent="rightCard" :maxCard="true">
                    <div slot="header">户信息</div>
                    <div id="RightBar" style="height:100%"></div>
                </techo-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import barOptions from "./config/barOptions.js";
import pieOptions from "./config/pieOptions.js";
import service from "./services/build_info_service.js";
export default {
  data() {
    return {
      DISCODE: "6102",
      // 房屋信息分布
      topBarOptions: {},
      topBarChart: null,
      // 房屋类型比例占比
      leftPieOptions: {},
      leftPieChart: null,
      // 户信息分布
      rightBarOptions: {},
      rightBarChart: null,
      // 当前柱状图级别code
      byCode: "",
      isShowLevelBtu: true,
      COLORS: ["#3965c3", "#ffa639", "#f75a51"]
    };
  },
  watch: {
    byCode(value) {
      this.isShowLevelBtu = value.length == 6 ? false : true;
    }
  },
  mounted() {
    let that = this;
    // 房屋信息分布
    this.topBarChart = this.$echarts.init(this.$el.querySelector("#TopBar"));
    // 房屋类型比例占比
    this.leftPieChart = this.$echarts.init(this.$el.querySelector("#LeftPie"));
    // 户信息分布
    this.rightBarChart = this.$echarts.init(
      this.$el.querySelector("#RightBar")
    );
    this.topBarChart.on("click", val => {
      if (this.byCode.length != 14) {
        this.getBuildingData(val.data.rawdata.code);
        this.getTypePieData(val.data.rawdata.code);
        this.getHouseBarData(val.data.rawdata.code);
      }
    });
    // 初始化统计图自适应函数
    this.$bus.$on("topCard", () => {
      that.topBarChart.resize();
    });
    this.$bus.$on("leftCard", () => {
      that.leftPieChart.resize();
    });
    this.$bus.$on("rightCard", () => {
      that.rightBarChart.resize();
    });
  },
  created() {
    this.getBuildingData();
    this.getTypePieData();
    this.getHouseBarData();
  },
  methods: {
    // 房屋信息
    getBuildingData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityNames: "BuildingInfo"
      };
      let that = this;
      this.topBarOptions = _.cloneDeep(barOptions);

      service.requestByEntityNames(param, data => {
        if (data.length == 0) return;
        this.byCode = data[0].code;
        this.topBarOptions.yAxis.name = "数量";
        this.topBarOptions.legend.data = ["数量"];
        this.topBarOptions.series.push({
          name: "数量",
          type: "bar",
          itemStyle: {
            normal: {
              color: that.COLORS[Math.floor(Math.random() * 3)]
            }
          },
          barMaxWidth: 80,
          data: []
        });
        _.map(data, (value, index) => {
          that.topBarOptions.xAxis.data.push(value.name);
          this.topBarOptions.series[0].data.push({
            rawdata: value,
            value: value.count
          });
        });
        this.topBarChart.setOption(that.topBarOptions);
        this.topBarChart.resize();
      });
    },
    // 房屋类型
    getTypePieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "BuildingInfo",
        fieldName: "type"
      };
      let that = this;
      this.leftPieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        data = data.map(item => {
          item.name = item.name == 1 ? "楼房" : "平房";
          return item;
        });
        that.leftPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.leftPieOptions.title.text = "房屋类型";
        that.leftPieOptions.series[0].name = "房屋信息";
        data = data.filter(item => item.count != 0);
        _.map(data, item => {
          that.leftPieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.leftPieOptions.series[0].radius = "45%";
        that.leftPieOptions.series[0].center = ["70%", "60%"];
        that.leftPieChart.setOption(that.leftPieOptions);
        that.leftPieChart.resize();
      });
    },
    // 户信息
    getHouseBarData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityNames: "BuildingHouse"
      };
      let that = this;
      this.rightBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityNames(param, data => {
        this.rightBarOptions.yAxis.name = "数量";
        this.rightBarOptions.legend.data = ["数量"];
        this.rightBarOptions.series.push({
          name: "数量",
          type: "bar",
          itemStyle: {
            normal: {
              color: that.COLORS[Math.floor(Math.random() * 3)]
            }
          },
          barMaxWidth: 80,
          data: []
        });
        _.map(data, (value, index) => {
          that.rightBarOptions.xAxis.data.push(value.name);
          this.rightBarOptions.series[0].data.push({
            rawdata: value,
            value: value.count
          });
        });
        this.rightBarChart.setOption(that.rightBarOptions);
        this.rightBarChart.resize();
      });
    },
    /** 返回上一级 */
    handleUpLevel() {
      let districtCode = "";
      switch (this.byCode.length) {
        case 9:
          districtCode = this.byCode.slice(0, 4);
          break;
        case 12:
          districtCode = this.byCode.slice(0, 6);
          break;
        case 14:
          districtCode = this.byCode.slice(0, 9);
          break;
        default:
          break;
      }
      this.getBuildingData(districtCode);
      this.getTypePieData(districtCode);
      this.getHouseBarData(districtCode);
    }
  }
};
</script>

<style lang="scss">
.charts-page {
  padding: 10px 5px;
  .top-card {
    height: 23rem;
  }
  .bottom-card {
    padding-top: 0.6rem;
    position: absolute;
    height: calc(100% - 24.2rem);
    width: 100%;
    .el-col-12 {
      height: 100%;
    }
  }
}
</style>